import { Component, Prop, h } from '@stencil/core';
import { BADGE_SHAPES, BADGE_SIZES, BADGE_VARIANTS } from './exports';

/**
 * @slot - For Label and Icon
 */
@Component({
  tag: 'mdx-badge',
  styleUrl: 'badge.scss',
  shadow: true,
})
export class Badge {
  @Prop({ reflect: true })
  variant: (typeof BADGE_VARIANTS)[keyof typeof BADGE_VARIANTS] = BADGE_VARIANTS.PRIMARY;

  @Prop({ reflect: true })
  size: (typeof BADGE_SIZES)[keyof typeof BADGE_SIZES] = BADGE_SIZES.MD;

  @Prop({ reflect: true })
  shape: (typeof BADGE_SHAPES)[keyof typeof BADGE_SHAPES] = BADGE_SHAPES.SQUARE;

  render() {
    return (
      <div class="badge">
        <span>{this.size !== BADGE_SIZES.DOT && <slot></slot>}</span>
      </div>
    );
  }
}
